import view from './View.js';
import model from './Model.js';

let controller = {
    init: function () {
        view.init();
    },

    createArea: function () {
        return model.app;
    },

    getRandomNumber: function getRandomNumber(start, end) {
        var range = end - start;
        var random = Math.random() * range;
        return start + random;
    },

    getRandomColor: function getRandomColor() {
        var letters = "0123456789ABCDEF";
        var color = "0x";
        for (var i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    },
    
    numberOfShapes: function() {
        model.counter = model.shapes().length;
        return model.counter;
    },

    getTotalSquare: function() {
        model.square = 0;
        model.shapes().forEach(function(shape) {
            model.square += shape.getSq();
        });
        view.showSquare.textContent = model.square;
        return model.square;
    },

    getRectangle: function(x, y) {
        var randomX = this.getRandomNumber(0, 700);
        var randomY = -100;
        var x = x || randomX;
        var y = y || randomY;
        var randomSize = this.getRandomNumber(50, 100);
    
        var rectangle = new PIXI.Graphics();
        rectangle.beginFill(this.getRandomColor());
        rectangle.drawRect(x, y, randomSize, randomSize);
        rectangle.endFill();
        rectangle.interactive = true;
        rectangle.buttonMode = true;
        rectangle.getSq = function () {
            return Math.floor(randomSize * randomSize);
        };
        rectangle.on("pointerdown", function () {
            rectangle.parent.removeChild(rectangle);
            controller.getTotalSquare();
        });
        return rectangle;
    },

    getEllipse: function(x, y) {
        var randomX = this.getRandomNumber(50, 750);
        var randomY = -100;
        var x = x || randomX;
        var y = y || randomY;
        var a = this.getRandomNumber(20, 50);
        var b = this.getRandomNumber(20, 50);
    
        var ellipse = new PIXI.Graphics();
        ellipse.beginFill(this.getRandomColor());
        ellipse.drawEllipse(x, y, a, b);
        ellipse.endFill();
        ellipse.interactive = true;
        ellipse.buttonMode = true;
        ellipse.getSq = function () {
            return Math.floor(3.1415 * a * b);
        };

        ellipse.on("pointerdown", function () {
            ellipse.parent.removeChild(ellipse);
            controller.getTotalSquare();
        });
        return ellipse;
    },

    getCircle: function getCircle(x, y) {
        var randomX =	this.getRandomNumber(50, 750);
        var randomY = -100;
        var x = x || randomX;
        var y = y || randomY;
        var radius = this.getRandomNumber(20, 50);
    
        var circle = new PIXI.Graphics();
        circle.beginFill(this.getRandomColor());
        circle.drawCircle(x, y, radius);
        circle.endFill();
        circle.interactive = true;
        circle.buttonMode = true;
        circle.getSq = function () {
            return Math.floor(3.1415 * (radius * radius));
        };

        circle.on("pointerdown", function () {
            circle.parent.removeChild(circle);
            controller.getTotalSquare();
        });
        return circle;
    },

    getTriangle: function (x, y) {
        var randomX = this.getRandomNumber(50, 750);
        var randomY = -100;
        var x = x || randomX;
        var y = y || randomY;
    
        let triangle = new PIXI.Graphics();
        triangle.beginFill(this.getRandomColor());
        triangle.drawPolygon([
            -32,
            64, //First point
            32,
            64, //Second point
            32,
            0 //Third point
        ]);
        triangle.endFill();
        triangle.x = x;
        triangle.y = y;
        triangle.interactive = true;
        triangle.buttonMode = true;
        triangle.getSq = function () {
            return 0.5 * (32 * 32);
        };
        triangle.on("pointerdown", function () {
            triangle.parent.removeChild(triangle);
            controller.getTotalSquare();
        });
        return triangle;
    },

    addShape: function (x, y) {
        var number = Math.floor(this.getRandomNumber(1, 5));
    
        if (number == 1) {
            view.app.stage.addChild( this.getCircle(x, y) );
        } else if (number == 2) {
            view.app.stage.addChild( this.getRectangle(x, y) );
        } else if (number == 3) {
            view.app.stage.addChild( this.getEllipse(x, y) );
        } else {
            view.app.stage.addChild( this.getTriangle(x, y) );
        }
    },

    inter: null,
    interval: function() {
        var self = this;
        clearInterval(this.inter);
        this.inter = setInterval(function() {
            self.addShape();
        },this.getShapesPerSecond());
    },

    getShapes: function() {
        return model.shapes();
    },

    getShapesPerSecond() {
        return Math.floor(1000/model.shapesPerSecond);
    },

    incCounter: function() {
        model.shapesPerSecond +=1;
        view.showShapesPerSecond.textContent = model.shapesPerSecond;
        this.interval();
    },

    decCounter: function() {
        if(model.shapesPerSecond > 1) {
            model.shapesPerSecond -=1;
        }
        view.showShapesPerSecond.textContent = model.shapesPerSecond;
        this.interval();
    },

    incGravity: function() {
        model.gravity +=1;
        view.gravityValue.textContent = model.gravity;
    },
    decGravity: function() {
        if(model.gravity > 1) {
            model.gravity -=1;
        }
        view.gravityValue.textContent = model.gravity;
    },

    getGravity: function() {
        return model.gravity;
    }
}


export default controller;